<template>

  <div>
    <h3>资源资源商编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col>
        <Form :model="formData" :label-width="100">
          <div style="width: 50%">
            <FormItem label="商品标题">
              <Input v-model="formData.name" placeholder="请填写商品标题"></Input>
            </FormItem>
            <FormItem label="商品简介">
              <Input v-model="formData.profile" type="textarea" placeholder="请填写商品简介"></Input>
            </FormItem>

            <FormItem label="商品描述">
              <Input v-model="formData.description" type="textarea" :rows="5" placeholder="请填写商品描述"></Input>
            </FormItem>
            <FormItem label="服务地址">
              <Input v-model="formData.address"  placeholder="请填写服务地址"></Input>
            </FormItem>
            <FormItem label="商品跳转地址">
              <Input v-model="formData.url" placeholder="请填写服务商品跳转地址"></Input>
            </FormItem>
            <FormItem label="商品价格">
              <InputNumber v-model="formData.price" style="width: 100%" placeholder="请填写服务商品价格"></InputNumber>
            </FormItem>
            <FormItem label="商品分组">
              <Select v-model="formData.group_id">
                <Option v-for="(item,index) in groups" v-bind:key="index" :value="item.id" :label="item.name">
                </Option>
              </Select>
            </FormItem>
            <FormItem label="状态">
              <RadioGroup v-model="formData.status">
                <Radio :label="0"><span>不可用</span></Radio>
                <Radio :label="1"><span>可用</span></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="是否推荐">
              <RadioGroup v-model="formData.recommend">
                <Radio :label="0"><span>不推荐</span></Radio>
                <Radio :label="1"><span>推荐</span></Radio>
              </RadioGroup>
            </FormItem>
            <FormItem label="封面图">
              <ImgSelect v-model="picture" :url="formData.picture">
              </ImgSelect>
            </FormItem>
          </div>

          <FormItem>
            <rich-view v-model="formData.rich_text"></rich-view>
          </FormItem>
          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>

        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {
        status: 1,
        recommend: 0
      },
      picture: null,
      load: false,
      groups: null
    }
  },
  mounted () {
    if (this.$route.query.id != null) {
      this.loadData()
    }
    this.loadGroup()
  },
  methods: {
    loadGroup () {
      api.spinner(res => {
        this.groups = res.data.data
      })
    },
    loadData () {
      this.load = true
      api.detail({ id: this.$route.query.id }, res => {
        this.load = false
        this.formData = res.data.data
        this.formData.space_id = this.formData.space.id
      }, () => {
        this.load = false
      })
    },
    submit () {
      let data = this.$formUtil.objToFormData(this.formData)
      if (this.picture == null && !this.$formUtil.isStrValid(this.formData.picture)) {
        this.$Message.error('请选择封面图')
        return
      } else if (this.picture != null) {
        data.append('picture', this.picture)
      }

      this.load = true
      api.save(data, () => {
        this.load = false
        this.$router.push({ name: 'service' })
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

